import React, { useState } from 'react'

import { Link, StaticQuery } from 'gatsby'
import Img from 'gatsby-image'

// import './blogSquare.css'
import { makeStyles } from '@material-ui/styles'
import { Typography, Paper } from '@material-ui/core'
import Box from '@material-ui/core/Box'
import { grey } from '@material-ui/core/colors'
import classnames from 'classnames'
import ImageIcon from '@material-ui/icons/Image'

const useStyles = makeStyles(theme => ({
  root: {
    position: 'relative',
    display: 'flex',
    flex: 1,
    flexDirection: 'column',
    minWidth: 250,
    maxWidth: 380,
    // marginBottom: 50,
    // paddingBottom: 50,
    // borderBottom: '1px solid #efefef',
  },
  paper: {
    overflow: 'hidden',
  },
  title: {
    margin: 0,
  },
  date: {
    margin: '5px 0px 15px',
    fontSize: '0.8em',
    fontWeight: 'bold',
  },
  link: { textDecoration: 'none' },
  readMore: {
    fontSize: '0.8em',
    display: 'inline-block',
  },
  titleImg: {
    height: 250,
    backgroundPosition: 'center',
    transition: 'all .5s',
  },
  titleImgPlaceholder: {
    height: 250,
    width: '100%',
    background: grey[200],
    overflow: 'hidden',
  },
  preview: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  scale: {
    transform: 'scale(1.2)',
  },
}))

const RecipeSquare = ({
  title,
  date,
  path,
  description,
  imageNode: titleImg,
}) => {
  const classes = useStyles()
  const [hover, setHover] = useState(false)
  return (
    <StaticQuery
      query={graphql`
        query TitleImageQuery {
          images: allFile(
            filter: {
              sourceInstanceName: { eq: "recipes" }
              relativePath: { regex: "/(title.png)/" }
            }
          ) {
            edges {
              node {
                relativePath
                relativeDirectory
                name
                extension
                dir
                modifiedTime
                childImageSharp {
                  # Specify the image processing specifications right in the query.
                  # Makes it trivial to update as your page's design changes.
                  fixed(width: 250) {
                    ...GatsbyImageSharpFixed
                  }
                  fluid(maxWidth: 1600) {
                    ...GatsbyImageSharpFluid
                  }
                }
              }
            }
          }
        }
      `}
      render={data => {
        return (
          <Box component={'section'} m={2} className={classes.root}>
            <Paper
              // square
              elevation={hover ? 0 : 0}
              // className={classes.paper}
              onMouseOver={() => setHover(true)}
              onMouseOut={() => setHover(false)}
            >
              <Link to={path} className={classes.link}>
                <Paper className={classes.paper} elevation={0}>
                  <div
                    className={classnames(classes.titleImgPlaceholder, {
                      [classes.preview]: !titleImg,
                    })}
                  >
                    {titleImg ? (
                      <Img
                        className={classnames(classes.titleImg, {
                          [classes.scale]: hover,
                        })}
                        fluid={titleImg.childImageSharp.fluid}
                        // fixed={titleImg.childImageSharp.fixed}
                        alt={title}
                        alt={`Image of ${title}`}
                      />
                    ) : (
                      <ImageIcon color="disabled" fontSize="large" />
                    )}
                  </div>
                </Paper>
                <Box mt={1}>
                  <Paper elevation={hover ? 0 : 0}>
                    <Box p={1}>
                      <Typography
                        component="h2"
                        variant="overline"
                        align="center"
                        className={classes.title}
                      >
                        {title}
                      </Typography>
                      {/* <p className={classes.date}>{date}</p>
                      <Typography variant="body2">{description}</Typography>
                      <p
                        className={classes.readMore}
                        style={{
                          fontSize: '.8em',
                          textDecoration: 'underline',
                        }}
                      >
                        Read more
                      </p> */}
                    </Box>
                  </Paper>
                </Box>
              </Link>
            </Paper>
          </Box>
        )
      }}
    />
  )
}

export default RecipeSquare
