import React from 'react'
import Layout from '../components/layout'
import { graphql } from 'gatsby'

import RecipeSquare from '../components/recipeSquare'
import { makeStyles } from '@material-ui/styles'

const useStyles = makeStyles(theme => ({
  root: {
    marginTop: 50,
    display: 'flex',
    flexDirection: 'row-reverse',
    justifyContent: 'space-around',
    flexWrap: 'wrap-reverse',
  },
}))

const RecipePage = ({ data }) => {
  const classes = useStyles()
  return (
    <Layout>
      <section className={classes.root}>
        {data.markdown.edges.map(post => {
          const image = (
            data.images.edges.find(({ node: image }) => {
              const checkPath = `/${image.relativePath}`
              return checkPath.includes(post.node.frontmatter.path.split("/recipes")[1])
            }) || {}
          ).node
          return (
            <RecipeSquare
              title={post.node.frontmatter.title}
              date={post.node.frontmatter.date}
              path={post.node.frontmatter.path}
              description={post.node.frontmatter.description}
              imageNode={image}
              key={post.node.id}
              SquareProps={{}}
              RecipeProps={{}}
            />
          )
        })}
      </section>
    </Layout>
  )
}
export const pageQuery = graphql`
  query RecipeIndexQuery {
    images: allFile(
      filter: {
        sourceInstanceName: { eq: "recipes" }
        relativePath: { regex: "/(title.png)/" }
      }
    ) {
      edges {
        node {
          relativePath
          relativeDirectory
          name
          extension
          dir
          modifiedTime
          childImageSharp {
            # Specify the image processing specifications right in the query.
            # Makes it trivial to update as your page's design changes.
            fixed(width: 250) {
              ...GatsbyImageSharpFixed
            }
            fluid(maxWidth: 1600) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    }
    markdown: allMarkdownRemark(
      sort: { order: ASC, fields: [frontmatter___date] }
      filter: { frontmatter: { section: { eq: "recipe" } } }
    ) {
      edges {
        node {
          id
          frontmatter {
            path
            title
            author
            date
            description
          }
        }
      }
    }
  }
`

export default RecipePage
